import Immutable from 'immutable';
import createReducer from '../../utils/createReducer';
import { APP_STARTED } from './app-action-types';

const initialState = Immutable.fromJS({});

export default createReducer(initialState, {
  [APP_STARTED]: (state) => {
    return state;
  }
});



